.App {
  text-align: center;
  height: 100vh;
  background-size: cover;
  background-position: bottom right;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  justify-content: space-between;
  padding: 10px;
}

.theme {
  font-size: 1rem;
}

.display {
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.messages {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 12px;
  padding: 10px;
  margin: 25px;
}

h2 {
  margin-top: 0px;
}

h3 {
  margin-bottom: 0;
}

.App-link {
  color: #61dafb;
}

.players {
  display: flex;
  flex-wrap: wrap;
  flex: auto;
}

.playerCard {
  padding: 10px;
  width: 400px;
  height: fit-content;
  margin: 10px;
  border-radius: 12px;
  background-image: url('./resources/players_bg.jpg');
  background-size: cover;
}

.control {
  display: flex;
  justify-content: space-between;
}

.normalDmg, .criticalDmg, .reductions {
  display: flex;
  padding: 5px;
  justify-content: space-between;
  align-items: center;
}

input[type=number] {
  width: 50px;
  border: 1px solid #555;
  background-color: rgba(255, 255, 255, 0.2);
}

input[type=text] {
  /* background-color: lightgrey; */
  background-color: transparent;
  border: 0;
  border-radius: 12px;
  padding: 5px;
  text-align: center;
  color: black;
  font-weight: bold;
}

input[type=text]::placeholder {
  color: black;
}

input[type=text]:focus {
  background-color: lightgrey;
}

button {
  border: 0;
  cursor: pointer;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  width: fit-content;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.4);
}

button:hover {
  box-shadow:inset 0px 0px 0px 1px rgb(0, 0, 0);
}

.rollBtn {
  display: flex;
  justify-content: center;
}

button[type=submit]>.dice1, button[type=submit]>.dice2 {
  transition: transform .7s ease-in-out;
}

button[type=submit]:hover>.dice1, button[type=submit]:hover>.dice2 {
  transform: rotate(360deg);
}

.addPlayer {
  background-image: url('./resources/add.png');
  background-color: transparent;
}

.rmvPlayer {
  background-image: url('./resources/rmv.png');
  background-color: transparent;
}